
import { ref, reactive, onBeforeMount, computed } from "vue";

export default {
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup() {
    var reload = ref<number>(0);
    var data = ref<number>(0);
    var nft = reactive({
      body: 0,
      clothe: 0,
      eye: 0,
      glasses: 0,
      hair: 0,
      mouse: 0,
      special: 0,
    });
    const rarity = computed(() => {
      var rarity = 0;
      if (nft.glasses != null) {
        rarity += 23;
      }
      if (nft.hair != null) {
        rarity += 21;
      }
      if (nft.special != null) {
        rarity += 26;
      }
      return rarity;
    });

    onBeforeMount(() => {
      randomNFT();
    });

    function getRandomInt(max) {
      if (max == 0) {
        return 0;
      }
      return Math.floor(Math.random() * max);
    }

    function sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    }

    async function randomNFT() {
      console.log("Taking a break...");
      let limitOfAttr = {
        body: 6,
        clothe: 10,
        eye: 8,
        glasses: 4, //can null
        hair: 10, //can null
        mouse: 4,
        special: 9, //can null
      };
      let canNullKey = ["glasses", "hair", "special"];
      for (const [key, value] of Object.entries(limitOfAttr)) {
        console.log(`${key}: ${value}`);
        if (canNullKey.includes(key)) {
          // random empty attr
          if (getRandomInt(100) > 70) {
            nft[key] = getRandomInt(value);
          } else {
            nft[key] = null;
          }
        } else {
          nft[key] = getRandomInt(value);
        }
      }
      reload.value += 1;
      if (reload.value > 30) {
        await sleep(100);
      } else {
        await sleep(80);
      }

      if (reload.value < 40) {
        randomNFT();
      } else {
        reload.value = 0;
      }
    }
    nft.body = getRandomInt(10);

    return { data, nft, randomNFT, reload, rarity };
  },
};
